$('.js-form-input-modal-placing').each((index, component) => {
	let $component = $(component);
	let $target = $(`[data-target="#${$component.attr('id')}"] .search-form-item__value`);
	let $submitButton = $component.find('.button-submit');

	function validateModal() {
		let $currentRadio = $component.find('input[type="radio"]:checked').parent().find('.input-radio__text');
		$target.text($currentRadio.text())
	}

	$submitButton.on('click', (e) => {
		e.preventDefault();

		validateModal();
	});
});
