import Swiper from 'swiper/dist/js/swiper.js';

$('.slider').each((index, component) => {
	let $component = $(component);
	let $swiperContainer = $component.find('.swiper-container');
	let sliderOptions = {
		slidesPerView: 1,
		spaceBetween: 0,
		autoHeight: false,
		navigation: {
			nextEl: '.slider__arrow_next',
			prevEl: '.slider__arrow_prev',
			disabledClass: 'slider__arrow_disabled',
		},
		pagination: {
			el: '.swiper-pagination',
			type: 'bullets'
		}
	};

	let swiper = new Swiper($swiperContainer.eq(0), sliderOptions);
});
