$('.js-form-input-modal-duration').each((index, component) => {
	let $component = $(component);
	let $target = $(`[data-target="#${$component.attr('id')}"] .search-form-item__value`);
	let $submitButton = $component.find('.button-submit');
	let $input = $component.find('input');

	function validateModal() {
		let inputArr = '';
		$input.each((index, item) => {
			inputArr += item.value;

			if (index === 0) {
				inputArr += ' - ';
			}
		});

		inputArr += ' ночей';

		console.log(inputArr);

		$target.text(inputArr);
	}

	$submitButton.on('click', (e) => {
		e.preventDefault();

		validateModal();
	});
});
