import flatpickr from "flatpickr";
import {Russian} from "flatpickr/dist/l10n/ru.js";

$('.date-input').each((index, component) => {
	let $component = $(component);
	let $input = $component.find('input');
	let $datesContainer = $component.find('.date-input__dates');
	let $datesArr = $component.find('.date-input__date');
	let monthArr = ['янв.', 'фев.', 'мар.', 'апр.', 'май', 'июн.', 'июл.', 'авг.', 'сен.', 'окт.', 'ноя.', 'дек']
	let weekArr = ['понедельник', 'вторник', 'среда', 'четверг', 'пятница', 'суббота', 'воскресенье'];

	function updatePicker(picker) {
		let dayContainers = picker.querySelectorAll('.dayContainer');

		dayContainers.forEach((elem) => {

			let month = elem.querySelector('.flatpickr-day:not(.prevMonthDay)');
			let monthArr = month.getAttribute('aria-label').split(' ');
			let currentMonth = `${monthArr[0]} ${monthArr[2]}`;

			let dayContainerMonth = document.createElement('div');
			dayContainerMonth.classList.add('date-input__title');
			dayContainerMonth.textContent = currentMonth;

			picker.insertBefore(dayContainerMonth, elem);
		});
	}

	function calcItemIndex(item, activeClass) {
		let parentContainer = item.parentElement;
		let days = parentContainer.querySelectorAll('.flatpickr-day');

		let dayIndex = 0;

		while (dayIndex < days.length) {
			if (days[dayIndex].classList.contains(activeClass)) {
				break;
			}
			dayIndex++;
		}

		return dayIndex;
	}

	function changeValues(picker) {
		let values = $input.val();

		values = values.split(' — ').map((value) => {
			let newValue = value.split('-').map((item) => parseInt(item));

			newValue[1] = monthArr[newValue[1]];

			return newValue;
		});

		$datesArr.each((index, item) => {
			let $bigText = item.querySelector('.date-input__date-big');
			let $smallText = item.querySelector('.date-input__date-small');

			if (values[index]) {
				$bigText.textContent = `${values[index][2]} ${values[index][1]} ${values[index][0]}`;

				if (index === 0) {
					let day = calcItemIndex(picker.querySelector('.startRange'), 'startRange');
					$smallText.textContent = weekArr[(day % 7)];
				}

				if (index === 1) {
					let day = calcItemIndex(picker.querySelector('.endRange'), 'endRange');
					$smallText.textContent = weekArr[(day % 7)];
				}
			} else {
				$bigText.textContent = '';
				$smallText.textContent = '';
			}
		});

		if ($datesArr.length) {
			$datesContainer.stop(true, true).addClass('visible');
		} else {
			$datesContainer.stop(true, true).removeClass('visible');
		}
	}

	let inputPicker = flatpickr($input[0], {
		inline: true,
		"locale": Russian,
		minDate: "today",
		mode: "range",
		showMonths: 6,
		static: true,
		onReady: function () {
			updatePicker(this.daysContainer);
		},
		onChange: function () {
			updatePicker(this.daysContainer);
		},
		onValueUpdate: function () {
			changeValues(this.calendarContainer);
		}
	});
});
