$('.js-form-input-modal-depart-date').each((index, component) => {
	let $component = $(component);
	let $target = $(`[data-target="#${$component.attr('id')}"] .search-form-item__value`);
	let $submitButton = $component.find('.button-submit');
	let $dateInput = $component.find('.date-input input');

	$submitButton.on('click', (e) => {
		e.preventDefault();

		$target.text($dateInput.val());
	});
});
